<template>
    <div>
        <loading :active.sync="isLoading" ></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4"  v-for="item in products" :key="item.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 400px; background-size: cover; background-position: center"
                    :style="{backgroundImage:`url(${item.imageUrl})`}"
                    >
                    </div>
                    <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{item.title}}</a>
                    </h5>
                    <p class="card-text">{{item.content}}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <div class="h5" v-if="!item.price">{{item.origin_price}}</div>
                        <del class="h6" v-if="item.price">{{item.origin_price}}</del>
                        <div class="h5" v-if="item.price">{{item.price}}</div>
                    </div>
                    </div>
                    <div class="card-footer d-flex">
                    <button type="button" class="btn btn-outline-secondary btn-sm"
                    @click ="getProduct(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                        
                    </button>
                    <button type="button" class="btn btn-outline-danger btn-sm ml-auto" 
                    @click="addtoCart(item.id)">
                        <i class="fas fa-spinner fa-spin " v-if="status.loadingItem === item.id"></i>
                        
                    </button>
                    </div>
                </div>
                </div>

        </div>
        <!--  -->
        <div class="my-5 row justify-content-center">
            <div class="my-5 row justify-content-center">
                <table class="table">
                <thead>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </thead>
                <tbody >
                    <tr  v-for="item in cart.carts" :key="item.id" >
                    <td class="align-middle" >
                        <button type="button" class="btn btn-outline-danger btn-sm"
                        @click="removeCartItem(item.id)"
                        >
                        <i class="far fa-trash-alt" ></i>
                        </button>
                    </td>
                    <td class="align-middle">
                        {{ item.product.title }}
                        <!--  -->
                        <div class="text-success" v-if="item.coupon">
                        
                        </div>
                        
                    </td>
                    <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="align-middle text-right">{{ item.final_total | currency }}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                    <td colspan="3" class="text-right"></td>
                    <td class="text-right">{{ cart.total  | currency}}</td>
                    </tr>
                    <tr v-if="cart.final_total !== cart.total">
                    <!-- colspantd -->
                    <td colspan="3" class="text-right text-success"></td>
                    <td class="text-right text-success">{{ cart.final_total | currency }}</td>
                    </tr> 
                </tfoot>
                </table>
                <div class="input-group mb-3 input-group-sm">
                    <input type="text" class="form-control" v-model="coupon_code" placeholder="">
                    <div class="input-group-append">
                        <button class="btn btn-outline-secondary" type="button" @click="addCouponCode" >
                        
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <!--  -->

        <div class="my-5 row justify-content-center">
        <form class="col-md-6" @submit.prevent="createOrder">
            <div class="form-group">
                <label for="useremail">Email</label>
                <input type="email" class="form-control" name="email" id="useremail"
                v-validate="'required|email'"
                :class="{'is-invalid': errors.has('email')}"
                v-model="form.user.email" placeholder=" Email" >
                <span class="text-danger" v-if="errors.has('email')">
                {{ errors.first('email') }}
                </span>
               <!-- errors.first...email -->

            </div>
        
            <div class="form-group">
                <label for="username"></label>
                <!-- :class{} -->
                <!-- errors.has(name) -->

                <input type="text" class="form-control" name="name" id="username"
                    :class="{'is-invalid': errors.has('name')}"  
                    v-model="form.user.name" v-validate="'required'" placeholder="">
                <span class="text-danger" v-if="errors.has('name')"></span>
                </div>
        
            <div class="form-group">
            <label for="usertel"></label>
                <input type="tel" class="form-control" name="tel" id="usertel" 
                :class="{'is-invalid': errors.has('tel')}"
                v-model="form.user.tel" placeholder=""
                v-validate="'required'" >
                <span class="text-danger" v-if="errors.has('tel')"></span>
                </div>
        
            <div class="form-group">
            <label for="useraddress"></label>
            <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                placeholder="">
            </div>
        
            <div class="form-group">
            <label for="comment"></label>
            <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
            </div>
            <div class="text-right">
            <button class="btn btn-danger"></button>
            </div>
        </form>
        </div>
        <!-- modal -->
        <div class="modal fade" id="productModal"
            tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">{{product.title}}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <img :src="product.imageUrl" class="img-fluid" alt />
                    <blockquote class="blockquote mt-3">
                    <p class="mb-0">{{ product.content }}</p>
                    <footer class="blockquote-footer text-right">{{ product.description }}</footer>
                    </blockquote>
                    <div class="d-flex justify-content-between align-items-baseline">
                    <del class="h6"> {{product.origin_price }}</del>
                    <div class="h5"> {{product.price}}</div>
                    </div>
                    <select class="form-control mt-3" v-model="product.num">
                    <option :value="num" v-for="num in 10" :key="num">
                        {{num}}{{product.unit}}
                    </option>
                    </select>
                </div>
                <div class="modal-footer">
                    <div class="text-muted text-nowrap mr-3">
                    
                    <strong>{{ product.num * product.price }}</strong> 
                    </div>
                    <button type="button" class="btn btn-primary" 
                    @click="addtoCart(product.id , product.num)"
                     ></button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import $ from 'jquery';
import Pagination from '@/components/Pagination';





export default {
    data(){
        return{
            products:[],
            product:{},
            status:{ //
                loadingItem:{}, //
            },
            cart: {},
            form:{
                user:{
                    name:'',
                    email:'',
                    tel:'',
                    address:'',
                },
                message:'',
            },

            coupon_code :'',
            isLoading: false,

        };
    },
    methods:{
        getProducts(){ 
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
            vm.isLoading = true;

            this.$http.get(api).then((response) => { 
                // console.log(response.data);
                vm.isLoading = false;
                vm.products = response.data.products; //productsdata

           });  
        },

        getProduct(id){ //clickmodel
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
            vm.status.loadingItem =id;

            this.$http.get(api).then((response) => { 
                //console.log(response);
                vm.product = response.data.product; //productsdata
                $('#productModal').modal('show');
                vm.status.loadingItem ='';
           });  
        },


        addtoCart(id,qty = 1){ 
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.status.loadingItem =id;
            const cart ={ //API
                product_id:id,
                qty,
            };
            this.$http.post(api,{data:cart}).then((response) => {  //APIdata:
                //console.log(response);
                vm.status.loadingItem ='';
                $('#productModal').modal('hide');
                vm.getCart(); //Cartdata
           });  
        },

        getCart(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.isLoading = true;

            this.$http.get(api).then((response) => { 
                console.log(response);
                vm.isLoading = false;
                vm.cart = response.data.data; 
           });  

        },

        removeCartItem(id){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
            vm.isLoading = true;

            this.$http.delete(api).then(() => { 
                vm.isLoading = false;
                vm.getCart(); //
            });  

        },
        addCouponCode(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
            const coupon ={
                code:vm.coupon_code,
            };
            vm.isLoading = true;

            this.$http.post(api , {data:coupon}).then((response) => { 
                //console.log(response);
                vm.isLoading = false;
                vm.getCart();
            });  
        },
        
        createOrder(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const order = vm.form ;  
            //vm.isLoading = true;

            this.$validator.validate().then((valid) => {
                if (valid) {
                    this.$http.post(api , {data:order}).then((response) => { 
                        console.log("" , response);
                        if (response.data.success) {
                            vm.$router.push(`/customer_checkout/${response.data.orderId}`); //orderId
                        }
                        vm.isLoading = false;
                    });  
                } else {
                    console.log('');
                }
                });
            },
    },



    created(){ 
        this.getProducts();
        this.getCart();
    }
};
</script>


