import Vue from 'vue'
import VueRouter from 'vue-router'
//import Home from '@/components/HelloWorld';
import Login from '@/components/pages/Login';
import Dashboard from '@/components/Dashboard';
import Products from '@/components/pages/Products';
import Order from '@/components/pages/Order';
import Coupon from '@/components/pages/Coupon';
import CustomerOrder from '@/components/pages/CustomerOrder';
import CustomerCheckout from '@/components/pages/CustomerCheckout';





//    

Vue.use(VueRouter);

export default new VueRouter({
    routes:[

        { //login
            path:'*',
            redirect:'login',
        },
        // {
        //     path:'/', //
        //     name:'', //
        //     component: Home, //
        //     meta: { requiresAuth: true } //true

        // },
        {
            path:'/login',
            name:'Login',
            component: Login,
        },
        {
            path:'/admin',
            name:'Dashboard',
            component: Dashboard,
            children:[
                {   path:'products', //http://admin/products
                    name:'products',
                    component: Products,
                    meta: { requiresAuth: true },
                },

                {   path:'orders', //http://admin/orders
                    name:'orders',
                    component: Order,
                },

                {   path:'coupons', //http://admin/coupons
                    name:'coupons',
                    component: Coupon,
                },
            ]

            
        },

        {
            path:'/',
            name:'Dashboard',
            component: Dashboard,
            children:[
                {   path:'customer_order', //http://admin/products //
                    name:'CustomerOrder',
                    component: CustomerOrder,
                },

                {   path:'customer_checkout/:orderId', //http://admin/products //
                    name:'CustomerCheckout',
                    component: CustomerCheckout,
                },

            ]

            
        },
    ],
});


